<template>
  <div id="box">
    <v-img
    src="../../public/img/textures/TextLogo.png"
    width="50%"
    id="loginImg"
    ></v-img>
    <login-box @swap="swapBox" v-if='login'/>
    <register-box @swap="swapBox" v-if='!login'/>
    <a @click="$router.push('Map')">Dev Login</a>
  </div>
</template>

<script>
import LoginBox from '../components/LoginBox.vue';
import RegisterBox from '../components/RegisterBox.vue';
export default {
  data(){
    return{
      login:true
    }
  },
    components: {
        LoginBox,
        RegisterBox
    },
  methods:{
    swapBox(){
      this.login=!this.login;
    }
  }
}
</script>

<style>
#box{
  position:relative;
  margin-right:3rem;
  margin-left:3rem;
  top: 40%;
  transform: translateY(-50%);
}
#loginImg{
  margin-bottom:3rem;
  margin-left:auto;
  margin-right:auto;
}
div[data-app='true'] {
  background: url('../../public/img/textures/LoginBackgroundMobile.jpg') !important;
  background-size: cover 100%;
  background-position: center;
  background-repeat: no-repeat;
}
</style>