<template>
  <div id="content">
    <v-text-field
            label="E-mail"
            class="transparent font-bold"
            :rules="emailRules"
            v-model="email"
            solo
            elevation="3"
          ></v-text-field>
    <v-text-field
            :rules="usernameRules"
            label="Username"
            hint="At least 3 characters"
            class="transparent font-bold"
            solo
            elevation="3"
          ></v-text-field>
    <v-text-field
            :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
            :rules="[passwordRules.required, passwordRules.min]"
            :type="show ? 'text' : 'password'"
            label="Password"
            hint="At least 8 characters"
            class="input-group--focused transparent font-bold"
            @click:append="show = !show"
            solo
            elevation="3"
          ></v-text-field>
    <v-checkbox
    dark
              v-model="TOS"
              color="white"
              value="white"
              hide-details
              id="registerCheckbox"
            ><span slot="label" style="color:white;">I accept the Terms of Service.</span>
    </v-checkbox>      
    <v-btn
      x-large 
      block 
      elevation="3" 
      color="white"
      class="font-bold"
      id="loginBtn"
      :disabled="!TOS"
    >Register</v-btn>
    <label
    style="color:white;"
    class="font-thin"
    id="loginLabel"
    >Already have an Account? 
    <span
    @click="$emit('swap')"
    class="font-bold"
    >Login here.</span></label>
  </div>
</template>

<script>
export default {
  data(){
    return{
      TOS:false,
      email:"",
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid',
      ],
      usernameRules: [
        v => !!v || 'Username is required',
        v => v.length >= 3 || 'Minimum 3 characters.',
      ],
      passwordRules: {
          required: value => !!value || 'Required.',
          min: v => v.length >= 8 || 'Minimum 8 characters',
      },
      show: false
    }
  }
}
</script>

<style>
#content{
  width:100%;
  height:100%;
}
#loginBtn{
  margin-bottom:1rem;
  text-align:center;
}
#loginLabel{
  margin-left:auto;
  margin-right:auto;
}
#registerCheckbox{
  color:white!important;
  margin-bottom:1rem;
}
.transparent{
  background-color: white!important;
  opacity: 0.7;
}
</style>