<template>
  <div>
    <div id="top-panel">
      <v-btn
        id="backbtn"
        fab
        x-large
        elevation="5"
        color="white"
        @click="$router.push('Map')"
      >
        <v-icon light>arrow-left</v-icon>
      </v-btn>
    </div>
    <div id="btn-panel">
      <v-btn
        id="filterbtn"
        x-large
        block
        elevation="5"
        color="white"
        class="font-bold centered"
        >Filter</v-btn
      >
    </div>
    <div id="list-panel">
      <List class="centered" :listShow="listShow" :items="events" />
    </div>
  </div>
</template>

<script>
import List from "../components/EventList.vue";
import axios from "axios";
export default {
  name: "EventMenu",
  components: {
    List,
  },
  data() {
    return {
      events: [],
      listShow: 10,
    };
  },
  async mounted() {
    await this.refreshEvents();
  },
  methods: {
    //Fetches Events from Server
    async refreshEvents() {
      let res = await axios.get("http://localhost:3000/frl");
      this.events = res.data;
      console.log("Fetched Events: " + this.events, this.events);
    },
  },
};
</script>

<style lang="scss" scoped>
div[data-app="true"] {
  background-color: white;
}
#list-panel {
  width: 100%;
  margin: auto;
}
.centered {
  margin: auto;
  padding-left: 3rem;
  padding-right: 3rem;
}
#top-panel {
  top: 1.5rem;
  left: 1.5rem;
}
</style>
